
"use client";
import { useEffect, useState } from 'react';

export default function DealsTable() {
  const [deals, setDeals] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/deals')
      .then(res => res.json())
      .then(data => {
        setDeals(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <div>Loading deals...</div>;

  return (
    <table className="data-table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Stage</th>
          <th>Amount</th>
          <th>Owner</th>
          <th>Expected Close</th>
        </tr>
      </thead>
      <tbody>
        {deals.map((d: any) => (
          <tr key={d.id}>
            <td>{d.name}</td>
            <td>{d.stageId}</td>
            <td>{d.amount} {d.currency}</td>
            <td>{d.ownerUserId}</td>
            <td>{new Date(d.expectedCloseDate).toLocaleDateString()}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
